<template>
  <div>
    <div class="d-flex justify-center mb-6 mt-6">
      <v-img
        src="@/assets/logoInfo.png"
        max-height="150"
        max-width="250"
      ></v-img>
    </div>
    <v-container>
      <Botones />
      <v-data-table
        :headers="headers"
        :items="clientes"
        :items-per-page="5"
        class="elevation-1 mt-8"
      >
      <template v-slot:item.actions="{ item }">
        <!-- <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon> -->
      <v-btn small class="mt-2">
        Editar
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn small class="mt-2 mb-2">
        Eliminar
      </v-btn>
      </template>
      </v-data-table>
    </v-container>
  </div>
</template>

<script>
import Botones from "@/components/Botones";
import axios from "axios";
export default {
  components: {
    Botones,
  },
  name: "VistaClientes",
  data() {
    return {
      headers: [
        { text: "Cliente", value: "cliente" },
        { text: "Fecha", value: "date" },
        { text: "Productos", value: "productos" },
        { text: "Cantidades", value: "cantidades" },
        // { text: 'Totales', value: 'totales' },
        { text: "Fecha de creacion", value: "createdAt" },
        { text: "Acciones", value: "actions" },
      ],
      clientes: [],
    };
  },
  created() {
    this.listarClientes();
  },
  methods: {
    listarClientes() {
      axios
        .get(this.$url + "/listarClientes")
        .then((result) => {
          console.log(result.data);
          this.clientes = result.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
